import { BunnyCloudUrl } from '../../../constants'
const SocialCustomStyle = (styleData) => {
    try {
        const styleId = "tb_socialfonts";
        if (!document.getElementById(styleId)) {
            const style = document.createElement('style');
            style.type = 'text/css';
            style.id = styleId;
            style.innerHTML = styleData
            document.head.appendChild(style)
        }
    }
    catch (e) {
    }
}
const SocialFonts = () => {
    const version = 4
    const WebFonts = `@font-face { font-family: 'WebSocialFonts'; src: url('${BunnyCloudUrl}/web-fonts/fonts-icon/WebFonts.ttf?var=${version}') format('truetype'), url('${BunnyCloudUrl}/web-fonts/fonts-icon/WebFonts.woff?var=${version}') format('woff'), url('${BunnyCloudUrl}/web-fonts/fonts-icon/WebFonts.svg?var=${version}') format('svg'); font-weight: normal; font-style: normal; font-display: swap;`
    return <>{SocialCustomStyle(WebFonts)}</>
}
export default SocialFonts
